.info--section{
    margin-inline: 1.7em;
    height: 120vh;
    /*background-color: lightcoral;*/
    color: #D9D9D9;
    overflow-y: hidden;
}

.info--img-container{
    display: flex;
    /*background-color: lightblue;*/
    justify-content: space-between;
    padding-inline: 2%;
    margin-bottom: 2em;
}

.info--heading{
    display: flex;
    flex-direction: column;
    font-size: 2.5rem;
}

.info--content .info--links > li{
    font-size: 1.2rem;
    padding-block: 3%;
    
}

ul > li::marker{
    color: white;
}

/* .info--links > li::before {
    content: "• ";
    color: rgb(255, 255, 255);
} */


@media only screen and (min-width: 720px) and (max-width: 920px){

    .info--heading{
        display: flex;
        font-size: 3rem;
        flex-direction: row;
        align-items: center;
        gap: 1em;
    }

    .info--content .info--links > li{
        font-size: 1.4rem;
    }

}

@media only screen and (min-width: 920px){

    .info--section{
        margin-inline: 8em;
        /*background-color: lightblue;*/
    }

    .info--sun {
        display: none;
    }
    
    .info--heading{
        display: flex;
        font-size: 5rem;
        flex-direction: row;
        align-items: center;
        gap: 1em;
    }

    .info--content .info--links > li{
        font-size: 1.8rem;
        padding: 0.7em;
    }

}