footer{
    height: 10vh;
    background-color: #191919;
    color: white;
    display: flex;
    justify-content: space-between;
    padding-inline: 8%;
    align-items: center;
}

.footer--text, .footer--links{
    display: flex;
    flex-direction: column;

}

@media only screen and (min-width: 720px) and (max-width: 920px){

    .footer--text, .footer--links{
        display: flex;
        flex-direction: column;
        font-size: 1.3rem;
    }
}

@media only screen and (min-width: 921px){

    footer{
        width: 100%;
    }

    .footer--text, .footer--links{
        display: flex;
        flex-direction: row;
        gap: 1em;
        font-size: 1.4rem;
    }

}

