*{
    overflow-x: hidden;
}

.hero--section{
    margin-inline: 1.7em;
    height: 90vh;
    /*background-color: lightblue;*/
    overflow-y: hidden;
}

.hero--section .hero--center {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    text-align: center;
    padding-top: 2vh;
}

.hero--section .hero--center .hero--img{
    max-width: 16rem;
    margin-bottom: 0.5em;
    animation: move 1s alternate infinite;
}

.hero--section .hero--center .hero--heading{
    color: #D9D9D9;
    font-size: 3rem;
    margin-bottom: 0.5em;
}

.hero--section .hero--center .hero--para{
    color: #D9D9D9;
    font-size: 1.2rem;
    margin-bottom: 1em;
}

.hero--section .hero--center .hero--button{
    margin-block: 1em;
    background-color: white;
}
.hero--section .hero--center .hero--button:active{
    background-color: rgb(242, 238, 255);
    transform: translateY(2px);
}

.hero--section .hero--center .hero--img--desktop{
    display: none;
} 

@media only screen and (min-width: 720px) and (max-width: 920px){


    .hero--section .hero--center .hero--para{
        color: #D9D9D9;
        font-size: 1.4rem;
        margin-bottom: 1em;
    }  
}


@media only screen and (min-width: 920px){

    .hero--section .hero--center .hero--img{
        display: none;
    }
    
    .hero--section{
        margin-inline: 8em;
        /*background-color: lightblue;*/
    }

    .hero--section .hero--center {
        position: unset;
        /*background-color: lightgreen;*/
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        align-items: flex-start;
        padding-top: 12vh;
        width: 100%;
        height: auto;
        text-align: left;
        overflow-y: hidden;
    }

    .hero--section .hero--center .hero--img--desktop{
        display: block;
        max-width: 66rem;
        object-fit: contain;
        z-index: -999;
        position: absolute;
        top: -30%;
        right: -30%;
        /*background-color: lightcoral;*/
        animation: a 4s ease-in-out;
    }

    .hero--section .hero--center .hero--heading{
        /*background-color: lightcyan;*/
        font-size: 6rem;
    }

    .hero--section .hero--center .hero--para{
        /*background-color: lightgoldenrodyellow;*/
        font-size: 1.8rem;
        margin-bottom: 2em;
        width: 50%;
    }

    .hero--section .hero--center .hero--button{
        background-color: white;
        margin-bottom: 2em;
        font-size: 1.5rem;
    }

    .hero--section .hero--center .hero--button:hover{
        background-color: transparent;
        color: white;
        transform: translateY(2px);
    }

    .hero--section .hero--center .hero--button:active{
        background-color: rgb(242, 238, 255);
        color: black;
        transform: translateY(2px);
    }

    
}


@keyframes move{
    0%{
        transform: translateY(-10px);
    }

    100%{
        transform: translateY(0);
    }
}

@keyframes a{
    0%{
        filter: blur(20px);
        transform: translateX(100%);
    }

    100%{
        filter: blur(0);
        transform: translateX(0);
    }
}

