.bold{
    font-weight: 600;
}

.explore--section{
    margin-inline: 1.7em;
    height: 100vh;
    /*background-color: lightcoral;*/
    color: #D9D9D9;
    overflow-y: hidden;
}

.explore--heading{
    font-size: 2.5rem;
    margin-bottom: 1em;
}

.explore--para{
    font-size: 1.2rem;
    margin-bottom: 1.5em;
}

.explore--planets{
    display: flex;
    justify-content: flex-start;
    font-size: 1.2rem;
    gap: 0.3em;
}

.explore--links > li{
    margin-bottom: 0.8em;
}

