@media only screen and (min-width: 720px) and (max-width: 920px){

    .explore--section{
        height: 100vh;
    }
    .explore--heading{
        font-size: 3rem;
        gap: 1em;
    }
    .explore--para{
        font-size: 1.4rem;
        margin-bottom: 1.5em;
    }

    .explore--planets{
        display: flex;
        justify-content: flex-start;
        font-size: 1.4rem;
        flex-wrap: wrap;
        gap: 3em;
    }

    /* .explore--img {
        margin: 0;
        max-width: 45vw;
        /*box-shadow: 0px 8px 15px -15px lightblue;*/
        /*animation: moveLeft 4s infinite ease-in;
    } */
}

@media only screen and (min-width: 921px){

    .explore--section{
        margin-inline: 8em;
        /*background-color: lightblue;*/
        height: 100vh;
    }

    .explore--heading{
        font-size: 5rem;
        gap: 1em;
    }
    .explore--para{
        font-size: 1.8rem;
        margin-bottom: 1.5em;
    }

    .explore--planets{
        display: flex;
        justify-content: flex-start;
        font-size: 1.8rem;
        flex-wrap: wrap;
        gap: 5em;
    }

    

