@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap');

a{
    text-decoration: none;
}

li{
    list-style: none;
}

nav{
    position: fixed;
    right: 0;
    top: 0;
    background: white;
    height: 100vh;
    width: 50%;
    z-index: 10;
    text-transform: uppercase;
    transform: translateX(100%);
    transition: transform .5s ease-in-out;
}    

nav ul{
    margin-top: 8rem;
}

nav ul a{
    color: #000000;
    padding: 0.75em 2em;
    display: block;
}

nav ul a:hover{
    background: rgb(192, 183, 183);;
}

nav .nav--close{
    float: right;
    margin: 1.7em;
    width: 2.5em;
}

header{
    display: flex;
    justify-content: space-between;
}

header img{
    margin: 1.7em;
    cursor: pointer;
}

header .nav--logo{
    background-color: white;
    width: 7.2rem;
    border-radius: 8px;
}

header .nav--menu{
    /*background-color: white;*/
    max-width: 1.7rem;
}

.open--nav{
    transform: translateX(0%);
}

@media only screen and (min-width: 920px){


header .nav--menu{
    display: none;
}

nav .nav--close{
    display: none;
}


header .nav--logo{
    margin-top: 1.7em;
    width: 10rem;
}

nav{
    transform: translateX(0);
    position: unset;
    width: auto;
    height: auto;
    background: none;
    display: block;
    color: black;
    
}

nav ul{
    display: flex;
    margin-top: 1.7em;
    
}

nav ul a{
    font-size: 1.5rem;
    color: #ffffff;
}

nav ul a:hover{
    background: none;
    text-decoration: underline;
}

}